#home-wrap{
    /* background-image: url('http://www.fullhdwpp.com/wp-content/uploads/streets-of-japan-26_www.FullHDWpp.com_.jpg'); */
    height: 100vh;
    background-image: url('https://i.pinimg.com/originals/6c/73/41/6c7341f8ceef532c3ecbb7420b6adeb3.jpg');
    /* background-image: url('http://getwallpapers.com/wallpaper/full/3/6/0/655906.jpg'); */
    /* background-image: url('https://sotoak.com/wallpapers/japan-world-wallpaper-full-hd-As-Wallpaper-HD.jpg'); */
    /* background-image: url('http://sf.co.ua/14/07/wallpaper-2915737.jpg'); */
    background-size: cover;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
#menu{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 50%;
    left: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: white;
    background-color: rgba(0, 0, 0, 0.3);
    /* */
  }
  
  #menu h1, #list{
    background: transparent;
    color: white;
    font-family: 'Courgette', cursive;
  }
  #menu h1{
    font-size: 80px;
    margin-bottom: 4rem;
  }
  #menu #list .item{
    margin: 2rem 0rem;
    font-size: 45px;
    list-style: none;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    padding: 2px 1rem;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    position: relative;
  }
  
  #link-wrap{
      color: white;
      text-decoration: none;
  }
  #menu #list .item:hover img{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  #menu #list .item span{
    margin-right: 1rem;
  
  }
  #menu #list .item span img{
    font-weight: bold;
    height: 50px;
    -webkit-transition: 1s;
    transition: 1s;
  }
  #menu #list .item::before{
    content: "";
    position: absolute;
    background-color: rgba(43, 254, 234, 0.2);
    height: 100%;
    width: 0%;
    left: 0%;
    /* transform: translate(-50%); */
    -webkit-transition: 1s;
    transition: 1s;
    border-radius: 20px;
  }
  #menu #list .item:hover::before{
    width: 80%;
  }
  #menu h1{
    overflow: hidden;
    position: relative;
    color: #DDD;
    -webkit-animation: 10s textAnimate_2 infinite linear;
            animation: 10s textAnimate_2 infinite linear;
  }
  #menu h1::before{
    content: 'CoderS nihonGo';
    color: #70b1b1;
    position: absolute;
    top: -10%;
    -webkit-animation: 10s textAnimate infinite linear;
            animation: 10s textAnimate infinite linear;
  }
  #home-wrap #avar{
    position: fixed;
    top: 2%;
    right: 2%;
    height: 60px;
    width: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #b95587;
    border-radius: 100%;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  #home-wrap #avar h3{
    margin: 0;
    padding: 0;
    color: #DDD;
    text-transform: uppercase;
  }
 
  @-webkit-keyframes textAnimate_2{
    0%{
        color: black;
    }
    20%{
        color: #315b96;
    }
    40%{
        color: black;
    }
    60%{
        color: #445c3c;
    }
    80%{
        color: #315b96;
    }
    100%{
        color: #445c3c;
    }
  }
 
  @keyframes textAnimate_2{
    0%{
        color: black;
    }
    20%{
        color: #315b96;
    }
    40%{
        color: black;
    }
    60%{
        color: #445c3c;
    }
    80%{
        color: #315b96;
    }
    100%{
        color: #445c3c;
    }
  }


  /* button */
  #home-wrap #auth{
    position: fixed;
    top: 1%;
    right: 5%;
  }
  #home-wrap #auth button{
    padding: 10px 20px;
    margin: 0.2rem;
    border-radius: 10px;
    outline: none;
    border: none;
    font-family: sans-serif;
    font-weight: bold;
  
  
  }
  #home-wrap #auth .wrap-button button{
    position: relative;
    -webkit-transition: 0.6s;
    transition: 0.6s;
  }
  #home-wrap #auth .wrap-button img{
    position: absolute;
    height: 50px;
    -webkit-transform: rotate(-30deg) translate(-60%);
            transform: rotate(-30deg) translate(-60%);
    left: 50%;
    top: 140%;
    -webkit-animation: 1s button-animation infinite linear;
            animation: 1s button-animation infinite linear;
  }
 
  #home-wrap #auth button:hover{
    background: #e8fde1;
  }
  @-webkit-keyframes button-animation{
    0%{
      top: 140%;
    }
    25%{
      top: 80%;
    }
    50%{
      top: 60%
    }
    75%{
      top: 80%;
    }
    100%{
      top: 140%;
    }

  }
  @keyframes button-animation{
    0%{
      top: 140%;
    }
    25%{
      top: 80%;
    }
    50%{
      top: 60%
    }
    75%{
      top: 80%;
    }
    100%{
      top: 140%;
    }

  }

  
